import Component from '../../Component.js';
import {ComponentPortTypes, ComponentValueTypes} from '../../ComponentTypes.js';

class ComponentModulo extends Component {
    constructor() {
        super();

        this.componentName = "modulo circuit";
        this.createPort("A", ComponentPortTypes.In, ComponentValueTypes.Number);
        this.createPort("B", ComponentPortTypes.In, ComponentValueTypes.Number);
        this.createPort("Result", ComponentPortTypes.Out, ComponentValueTypes.Number);
        this.createAction("Compute", this.compute);
        this.createAction("On Computed", this.onComputed);
    }

    compute(component, action) {
        let a = component.getPort("A").value;
        let b = component.getPort("B").value;

        if (a != null && b != null) {
            component.getPort("Result").setValue(a % b)
            component.runAction("On Computed");
        }
    }

    onComputed() {

    }
}

export default ComponentModulo;


