import Component from '../../Component.js';
import {ComponentPortTypes, ComponentValueTypes} from '../../ComponentTypes';

class ComponentSensor extends Component {
    constructor() {
        super();

        this.componentName = "sensor";
        this.createPort("Ignore Storage", ComponentPortTypes.In, ComponentValueTypes.Bool);
        this.createPort("Scanned", ComponentPortTypes.Out, ComponentValueTypes.Reference);
        this.createAction("On Scanned", this.onScanned);
    }

    onScanned(component, action) {

    }
}

export default ComponentSensor;


