import Component from '../../Component.js';
import {ComponentPortTypes, ComponentValueTypes} from '../../ComponentTypes';

class ComopnentMemory extends Component {
    constructor(memoryCount, componentName) {
        super();

        this.memoryCount = memoryCount;
        this.componentName = componentName;
        for (let i=1; i<=this.memoryCount; i++) {
            this.createPort("Input " + i.toString(10), ComponentPortTypes.In, ComponentValueTypes.Any);;
            this.createPort("Output" + i.toString(10), ComponentPortTypes.Out, ComponentValueTypes.Any);
        }
        this.createAction("Set", this.set);
        this.createAction("On Set", this.onSet);
    }

    set(component, action) {
        for (let i=1; i<=component.memoryCount; i++) {
            component.getPort("Output " + i.toString(10)).setValue(component.getPort("Input " + i.toString(10)).value);
        }
        component.getAction("On Set").act();
    }

    onSet(component, action) {

    }
}

export default ComopnentMemory;


