import Component from '../../Component.js';
import {ComponentPortTypes, ComponentValueTypes} from '../../ComponentTypes';

class ComponentMedicalScanner extends Component {
    constructor() {
        super();

        this.componentName = "integrated medical analyser";
        this.createPort("Target", ComponentPortTypes.In, ComponentValueTypes.Reference);
        this.createPort("Total Health %", ComponentPortTypes.Out, ComponentValueTypes.Number);
        this.createPort("Total Health Missing", ComponentPortTypes.Out, ComponentValueTypes.Number);
        this.createAction("Scan", this.scan);
        this.createAction("On Scan", this.onScan);
    }

    scan(component, action) {
        component.getPort("Total Health %").setValue(100);
        component.getPort("Total Health Missing").setValue(0);
        component.getAction("On Scan").act();
    }

    onScan(component, action) {

    }
}

export default ComponentMedicalScanner;


