import Component from '../../Component.js';
import {ComponentPortTypes, ComponentValueTypes} from '../../ComponentTypes';

class ComponentMultiplexer extends Component {
    constructor(inputCount, componentName) {
        super();

        this.inputCount = inputCount;
        this.componentName = componentName;
        for (let i=1; i<= this.inputCount; i++) {
            this.createPort("Input " + i.toString(10), ComponentPortTypes.In, ComponentValueTypes.Any);
        }
        this.createPort("Input Selection", ComponentPortTypes.In, ComponentValueTypes.Number);
        this.createPort("Output", ComponentPortTypes.Out, ComponentValueTypes.Any);
        this.createAction("Select", this.select);
        this.createAction("On Select", this.onSelect);
    }

    select(component, action) {
        let inputSelectionComponent = component.getPort("Input Selection");

        if (inputSelectionComponent != null) {
            let inputSelection = inputSelectionComponent.value;

            if (inputSelection > component.inputCount || inputSelection <= 0 || inputSelection == null) {
                return;
            } else {
                component.getPort("Output").setValue(component.getPort("Input " + inputSelection.toString(10)).value);
                component.getAction("On Select").act();
            }
        }
    }

    onSelect(component, action) {

    }
}

export default ComponentMultiplexer;


