import Component from '../../Component.js';
import {ComponentPortTypes, ComponentValueTypes} from '../../ComponentTypes';

class ComponentLocomotion extends Component {
    constructor() {
        super();

        this.componentName = "locomotion circuit";
        this.createPort("Direction", ComponentPortTypes.In, ComponentValueTypes.Direction);
        this.createAction("Step Towards Direction", this.stepTowardsDirection);
        this.createAction("On Step", this.onStep);
        this.createAction("Blocked", this.blocked);
    }

    stepTowardsDirection(component, action) {
        component.getAction("On Step").act();
    }

    onStep(component, action) {

    }

    blocked(component, action) {
        
    }
}

export default ComponentLocomotion;


