import Component from '../../Component.js';
import {ComponentPortTypes, ComponentValueTypes} from '../../ComponentTypes';

class ComponentLargePowerTransmitter extends Component {
    constructor() {
        super();

        this.componentName = "large power transmission circuit";
        this.createPort("Target", ComponentPortTypes.In, ComponentValueTypes.Reference);
        this.createPort("Target Cell Charge", ComponentPortTypes.Out, ComponentValueTypes.Number);
        this.createPort("Target Cell Max Charge", ComponentPortTypes.Out, ComponentValueTypes.Number);
        this.createPort("Target Cell Percentage", ComponentPortTypes.Out, ComponentValueTypes.Number);
        this.createAction("Transmit", this.transmit);
        this.createAction("On Transmit", this.onTransmit);
    }

    transmit(component, action) {
        component.getAction("On Transmit").act();
    }

    onTransmit(component, action) {
        
    }
}

export default ComponentLargePowerTransmitter;


